import styled from 'styled-components';

export default styled.button`
  display: block;
  margin: 0;
  padding: 0;
  border: 0;
  background: #34495f;
  color: #fff;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 300;
  outline: none;
  cursor: pointer;
  user-select: none;
`;



